/* ===== Global ===== */
* {
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

body {
  margin: 0;
  background: #f2f3f5;
}

/* ===== Layout ===== */
.layout {
  display: grid;
  grid-template-columns: 240px 1fr 300px;
  max-width: 1400px;
  margin: 0 auto;
  gap: 20px;
  padding: 20px;
}

main {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* ===== Sidebars ===== */
.sidebar {
  background: #ffffff;
  border-radius: 12px;
  padding: 16px;
  height: fit-content;
}

.sidebar h4 {
  margin: 0 0 12px;
  font-size: 14px;
  color: #6b7280;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar li {
  padding: 10px 0;
  font-size: 14px;
  color: #374151;
  cursor: pointer;
}

/* ===== Post Creator ===== */
.post-creator {
  background: #ffffff;
  border-radius: 14px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.post-creator textarea {
  resize: none;
  border: none;
  outline: none;
  font-size: 15px;
  min-height: 70px;
  background: #f3f4f6;
  padding: 12px;
  border-radius: 10px;
}

.post-creator button {
  align-self: flex-end;
  background: #2563eb;
  color: #ffffff;
  border: none;
  padding: 8px 18px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
}

.post-creator button:hover {
  background: #1e40af;
}

/* ===== Timeline Tabs ===== */
.tabs {
  background: #ffffff;
  border-radius: 12px;
  padding: 6px;
  display: flex;
  justify-content: center;
  gap: 6px;
}

.tabs button {
  border: none;
  background: transparent;
  padding: 8px 18px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  color: #6b7280;
}

.tabs button.active {
  background: #e5e7eb;
  color: #111827;
  font-weight: 500;
}

/* ===== Posts ===== */
.post {
  background: #ffffff;
  border-radius: 14px;
  padding: 16px;
  font-size: 15px;
  color: #111827;
  line-height: 1.5;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

/* ===== Responsive (Optional but Safe) ===== */
@media (max-width: 1100px) {
  .layout {
    grid-template-columns: 200px 1fr;
  }

  .layout > :last-child {
    display: none;
  }
}
